.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App {
  background-color: #f8f8f8;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.input {
  background: rgb(236,46,25);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background: linear-gradient(90deg, rgba(236,46,25,1) 0%, rgba(238,120,28,1) 65%, rgba(255,132,0,1) 100%);
  color: white; 
  border: 0pt;
  height: 5% +5vh;
  width: 3% + 21vw;
  font-size: 5vh;
  border-radius: 3vmax;
  text-align: center;
  display: flex;
  box-shadow: 10px 10px 5px grey;

}

.result {

  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background: rgb(153,51,222);
  background: linear-gradient(90deg, rgba(153,51,222,1) 0%, rgba(62,29,253,1) 100%);
  color: white; 
  border: 0pt;
  height: 5% + 5vh;
  width: 3% + 21vw;
  font-size: 5vh;
  border-radius: 3vmax;
  box-shadow: 10px 10px 5px grey;


}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
